<template>
  <div>
    <div class="headbar">
    </div>
    <div class="bodybar">
      <div class="top">
        <img :src="yj.yjImg">
        <div class="overwrap"></div>
        <div class="p">
          <div class="title">·<span v-text="yj.yjName"></span></div>
          <div class="author" v-text="yj.yjAuthor"></div>
          <div class="time" v-text="yj.yjTime"></div>
        </div>
        <div class="next-btn">
          <a href=""><img src="../../images/down.png" alt=""></a>
        </div>
      </div>
      <div contenteditable="false" class="content">
        <vue-html5-editor contenteditable="false" :content="content" :height="500" :z-index="1000" :auto-height="true"></vue-html5-editor>
      </div>
      <div class="like">
        <a href="javascript:;" @click=" showa ('likeA',1) " v-if=" show === 'like' ">
          <img src="../../images/like.png">
        </a>
        <a href="javascript:;" @click=" showa ('like',-1) " v-if=" show === 'likeA' ">
          <img src="../../images/likeA.png">
        </a>
        <span v-text="yj.yjLikeNum"></span>
      </div>
    </div>
    <div class="footbar">
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        show: 'like',
        yj: {
          yjLikeNum: 3,
          yjid: 1,
          yjName: '故地重游 余村1',
          yjAuthor: '阿水12344',
          yjTime: '2017/7/2',
          yjImg: '',
          yjCon: ''
        },
        shuju: '',
        content: ''
      }
    },
    /* 游记通过传入所有参数显示 */
    created () {
      this.menun()
      this.shuju = this.GetQueryString('id')
      this.yj.yjAuthor = decodeURI(this.GetQueryString('author'))
      this.content = decodeURI(this.replaceFun2(this.GetQueryString('content')))
      this.yj.yjid = this.GetQueryString('id')
      this.yj.yjImg = decodeURI(this.replaceFun1(this.GetQueryString('mainpic')))
      this.yj.yjName = decodeURI(this.GetQueryString('name'))
      this.yj.yjTime = this.replaceFun1(this.GetQueryString('time'))
      this.yj.yjLikeNum = 0
      this.displayNone()
    },
    methods: {
      showa (str, num) {
        this.show = str
        this.yj.yjLikeNum += num
      },
      menun () {
        window.scrollTo(0, 0)
        /* window.alert(window.location.href) */
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      /* 去除vue-html5-editor的样式 */
      displayNone () {
        setTimeout(
          function () {
            var toolbar = document.getElementsByClassName('toolbar')[0]
            var editor = document.getElementsByClassName('vue-html5-editor')[0]
            toolbar.style.display = 'none'
            editor.style.border = '0px solid white'
            console.log('执行了displayNone')
            var contentdiv = document.getElementsByClassName('content')
            for (var icontent = 0; icontent < contentdiv.length; icontent++) {
              contentdiv[icontent].contentEditable = 'false'
            }
          }, 30
        )
      },
      /* 参数vue-html5-editor内容的格式化 */
      replaceFun2 (str) {
        if (str !== null) {
          return str.replace(/%3D/g, '=').replace(/%3B/g, ';').replace(/%3A/g, ':').replace(/%2F/g, '/')
        } else {
          return null
        }
      },
      /* 参数封面的格式化 */
      replaceFun1 (str) {
        if (str !== null) {
          return str.replace(/%3A/g, ':').replace(/%2F/g, '/')
        } else {
          return null
        }
      }
    }
  }
</script>

<style scoped>
  @import '../../styles/style.css';
  .top{
    width:100%;
    height:590px;
    position: relative;
  }
  .top>img{
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .top .overwrap{
    width:100%;
    height:100%;
    position: absolute;
    background: rgba(48,48,48,0.45);
  }
  .top .p{
    min-width: 40%;
    height: 50%;
    right: 0px;
    top:50%;
    transform: translateY(-50%);
    position: absolute;
    padding-right: 150px;
    text-align: right;
  }
  .top .p .title{
    font-size: 52px;
    font-weight: bold;
    color: #ffffff;
    height:60%;
    line-height: 4;
  }
  .top .p .author{
    color: #ffffff;
    font-weight: bold;
    font-size: 22px;
    height: 20%;
  }
  .top .p .time{
    color: #aaa;
    height:20%;
    font-size: 22px;
  }
  .top .next-btn{
    width: 100%;
    height: auto;
    position: absolute;
    text-align: center;
    bottom: 0;
  }
  .top .next-btn img{
    width: 110px;
    height: 60px;
  }
  .content{
    width: 1100px;
    height: auto;
    margin:0 auto;
    min-height: 550px;
    padding: 50px 100px;
    box-sizing: border-box;
  }
  .content img{
    width: 80%;
    height: auto;
    margin: 20px 10%;
  }
  .content p{
    line-height: 2;
  }
  .like{
    width: 80px;
    height:40px;
    line-height: 40px;
    margin:50px auto;
    text-align: center;
  }
  .like span{
    height:40px;
    line-height: 40px;
    width: 40px;
    display: block;
    float: left;
  }
  .like a img{
    height:40px;
    width: 40px;
    line-height: 40px;
  }
  .like a{
    width: 40px;
    height: 40px;
    display: block;
    float: left;
  }
</style>



