<template>
  <div class="warp">
    <div class="topbar">
      <div class="topimg imgcenter">
        <img src="../../images/view.jpg" alt="">
      </div>
      <div class="topyin"></div>
      <div class="search">
        <input type="text" placeholder="全部乡村">
        <button type="button"><img src="../../images/search.png" alt=""></button>
      </div>
      <div class="selectbar">
        <div class="noselect">
          <ul>
            <span class="ultitle">类型:</span>
            <li :class="{liactive: selectbar1 === 'all'}" @click="selectbar1='all'">全部</li>
            <li :class="{liactive: selectbar1 === 'tab1'}" @click="selectbar1='tab1'">文化传承</li>
            <li :class="{liactive: selectbar1 === 'tab2'}" @click="selectbar1='tab2'">生态保护</li>
            <li :class="{liactive: selectbar1 === 'tab3'}" @click="selectbar1='tab3'">产业发展</li>
          </ul>
          <ul>
            <span class="ultitle">筛选:</span>
            <li :class="{liactive: selectbar2 === 'all'}" @click="selectbar2='all'">全部</li>
            <li :class="{liactive: selectbar2 === 'tab1'}" @click="selectbar2='tab1'">热门</li>
            <li :class="{liactive: selectbar2 === 'tab2'}" @click="selectbar2='tab2'">最新</li>
            <li :class="{liactive: selectbar2 === 'tab3'}" @click="selectbar2='tab3'">推荐</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="bodybar" id="bodybar">
      <div class="body2">
        <div class="cell">
          <div class="cellimg">
            <img class="transition3" src="../../images/special1.jpg" alt="">
          </div>
          <div class="cellyin"></div>
          <div class="celltext transition3">
            高家堂村
          </div>
          <div class="celltext2 transition3">
            <a href="##">
              <img src="../../images/search.png" alt="">
            </a>
          </div>
        </div>
        <div class="cell">
          <div class="cellimg">
            <img class="transition3" src="../../images/special2.jpg" alt="">
          </div>
          <div class="cellyin"></div>
          <div class="celltext transition3">
            马家弄村
          </div>
          <div class="celltext2 transition3">
            <a href="##">
              <img src="../../images/search.png" alt="">
            </a>
          </div>
        </div>
        <div class="cell">
          <div class="cellimg">
            <img class="transition3" src="../../images/special3.jpg" alt="">
          </div>
          <div class="cellyin"></div>
          <div class="celltext transition3">
            余村村
          </div>
          <div class="celltext2 transition3">
            <a href="##">
              <img src="../../images/search.png" alt="">
            </a>
          </div>
        </div>
        <div class="cell">
          <div class="cellimg">
            <img class="transition3" src="../../images/special4.jpg" alt="">
          </div>
          <div class="cellyin"></div>
          <div class="celltext transition3">
            双一村
          </div>
          <div class="celltext2 transition3">
            <a href="##">
              <img src="../../images/search.png" alt="">
            </a>
          </div>
        </div>
        <div class="cell">
          <div class="cellimg">
            <img class="transition3" src="../../images/special4.jpg" alt="">
          </div>
          <div class="cellyin"></div>
          <div class="celltext transition3">
            横山坞村
          </div>
          <div class="celltext2 transition3">
            <a href="##">
              <img src="../../images/search.png" alt="">
            </a>
          </div>
        </div>

        <div class="clear"></div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        selectbar1: 'all',
        selectbar2: 'all'
      }
    },
    methods: {
      test () {
        window.alert(1)
      },
      pic () {
        debugger
//        const divContent = window.document.querySelector('cell');
//        const imgObj = window.getComputedStyle(divContent, null);
        let object = document.querySelector('.bodybar');
        let imgs = object.getElementsByTagName('img')
//        let imgdiv = object.getElementsByClassName('cellimg')[0]
        var imgwidth = 250
        var imgheight = 250
        let imgbili = imgwidth / imgheight
        for (var imgsI = 0; imgsI < imgs.length; imgsI++) {
          const imgsObj = window.getComputedStyle(imgs[imgsI], null);
          var imgsIwidth = parseInt(imgsObj['width'])
          var imgsIheight = parseInt(imgsObj['height'])
          console.log(imgsIwidth + '  ' + imgsIheight)
          if (imgsIwidth / imgsIheight > imgbili) {
            imgs[imgsI].style.height = imgheight + 'px'
            console.log('111')
          } else if (imgsIwidth / imgsIheight <= imgbili) {
            imgs[imgsI].style.width = imgwidth + 'px'
            console.log('222')
          }
        }
      }
    },
    created () {
    },
    mounted () {
      let object = document.querySelector('#bodybar');
    }
  }
</script>
<style scoped lang="stylus">
  @import url(../../styles/style.css);
  @import url(../../styles/move.css);
  .warp{
    width: 100%;
    height: auto;
  }
  .topbar{
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;
  }
  .topimg{
    overflow: hidden;
    width: 100%;
    position: absolute;
  }
  .topbar .topimg img {
    width: 100%;
  }
  .topyin{
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0,0,0,0.5);
  }
  .search{
    width: 430px;
    height: 50px;
    position: absolute;
    border: 1px solid #FFDE3A;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .search input{
    background: transparent;
    border: 0px solid white;
    height: 50px;
    line-height: 50px;
    width: 380px;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px 0px 0px 15px;
    display: block;
    float: left;
    outline: none;
    color: white;
  }
  .search button{
    width: 50px;
    height: 50px;
    background: #FFDE3A;
    border: 0px solid white;
    box-sizing: border-box;
    display: block;
    float: left;
  }
  .selectbar{
    height: 100px;
    position: absolute;
    left: 50%;
    top: 75%;
    transform: translate(-50%,-50%);
  }
  .selectbar ul{
       width: auto;
       height: 50px;
       line-height: 50px;
       padding: 0px 0px;
       clear: both;
     }
  .selectbar ul li{
    width: auto;
    height: 30px;
    line-height: 30px;
    padding: 0px 5px;
    margin: 10px 5px;
    float: left;
    cursor: pointer;
    color: #999;
  }
  .selectbar ul .liactive{
    border-bottom: 2px solid #FFDE3A;
    color: #fff;
  }
  .selectbar ul .ultitle{
    width: 60px;
    height: 30px;
    line-height: 30px;
    margin: 10px 0px;
    text-align-last: justify;
    padding-right: 10px;
    /*border-right: 2px solid #999;*/
    display: block;
    float: left;
    color: #999;
  }
  .bodybar{
    width: 100%;
    height: auto;
  }
  .bodybar .body2{
    width: 1100px;
    height: auto;
    min-height: 200px;
    margin: 50px auto 50px;
    clear: both;
    padding: 30px 10px;
    border-top: 2px solid #cccccc;
    box-sizing: border-box;
  }
  .bodybar .body2 .cell{
    width: 250px;
    height: 250px;
    float: left;
    margin: 10px;
    position: relative;
  }
  .bodybar .body2 .cell .cellimg{
    width: 250px;
    height: 250px;
    overflow: hidden;
    position: absolute;
  }
  .bodybar .body2 .cell .cellimg img{
    width: 300px;
    height: 300px;
    margin-left: -25px;
    margin-top: -25px;
  }
  .bodybar .body2 .cell .cellyin {
    width: 250px;
    height: 250px;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s;
  }
  .bodybar .body2 .cell .celltext{
    width: 250px;
    height: 50px;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
    font-size: 26px;
    font-weight: bold;
    line-height: 50px;
    letter-spacing:7px;
  }
  .bodybar .body2 .cell .celltext2{
    width: 32px;
    height: 32px;
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    line-height: 50px;
    opacity: 0;
    transition: All 0.4s ease-in-out;
    cursor: pointer;
  }
  .bodybar .body2 .cell:hover .cellyin{
    opacity: 1;
  }
  .bodybar .body2 .cell:hover .cellimg img{
    transform: scale(0.85);
  }
  .bodybar .body2 .cell:hover .celltext{
    transform: translateY(-50px);
  }
  .bodybar .body2 .cell:hover .celltext2{
    transform: translate(-50%,-50px);
    opacity: 1;
  }
</style>



