<template>
  <div>
  	<router-view></router-view>
  	<div class="main_re clear">
	  	<div class="main_le clear">
			<!-- 评论 -->
				<div class="hengg1 clear"></div>
				<!-- 评分处 -->
				<div class="stars">
					<ul>
						<li>评分：</li>
						<li @click=" showStar (1) " v-if=" 1 <= starNum ">
							<img src="../../icons/flo_s.png"></li>
						<li @click=" showStar (2) " v-if=" 2 <= starNum ">
							<img src="../../icons/flo_s.png"></li>
						<li @click=" showStar (3) " v-if=" 3 <= starNum ">
							<img src="../../icons/flo_s.png"></li>
						<li @click=" showStar (4) " v-if=" 4 <= starNum ">
							<img src="../../icons/flo_s.png"></li>
						<li @click=" showStar (5) " v-if=" 5 <= starNum ">
							<img src="../../icons/flo_s.png"></li>
						<li @click=" showStar (1) " v-if=" 1 > starNum ">
							<img src="../../icons/flo_k.png"></li>
						<li @click=" showStar (2) " v-if=" 2 > starNum ">
							<img src="../../icons/flo_k.png"></li>
						<li @click=" showStar (3) " v-if=" 3 > starNum ">
							<img src="../../icons/flo_k.png"></li>
						<li @click=" showStar (4) " v-if=" 4 > starNum ">
							<img src="../../icons/flo_k.png"></li>
						<li @click=" showStar (5) " v-if=" 5 > starNum ">
							<img src="../../icons/flo_k.png"></li>
					</ul>
					<div><span>{{starNum}}.0</span>分</div>
				</div>
				<!-- 评论处 -->
				<form>
					<textarea class="remark_textarea" rows="13" placeholder="我有独特的见解..." v-model="commentText"></textarea>
					<input type="submit" class="clear remark_sub" value="提交" @click.prevent="addComment">
				</form>
				<!-- 用户的评论 -->
				<div class="re_notice" v-if="comment.length == 0">
					暂无评论，我来发表第一篇评论！
				</div>
				<div class="remark_con clear">
					<div class="one_rem clear"  v-for="(item,index) in comment" v-bind:index="index" >
						<div class="remark_infor">
							<div class="user_hp">
								<img :src="item.headp">
							</div>
							<div class="na_time">
								<div class="user_na">{{item.name}}</div>
								<div class="time_re">{{item.time}}</div>
							</div>
							<div class="like_dis">
								<ul>
									<li v-if="item.like_show === 'like'" @click="ChangeLike(item,'liked',1)"><img src="../../icons/like.png"></li>
									<li v-if="item.like_show === 'liked'" @click="ChangeLike(item,'like',-1)"><img src="../../icons/liked.png"></li>
									<li>{{item.like}}</li>
									<li v-if="item.dislike_show === 'dislike'" @click="ChangeDislike(item,'disliked',1)"><img src="../../icons/dislike.png"></li>
									<li v-if="item.dislike_show === 'disliked'" @click="ChangeDislike(item,'dislike',-1)"><img src="../../icons/disliked.png"></li>
									<li>{{item.dislike}}</li>
								</ul>
							</div>
						</div>
						<div class="remark_text clear">
							<div class="or_con">{{item.content}}</div>
							<div class="hengg2"></div>
							<div class="or_score clear">
								<ul>
									<li v-if=" 1 <= item.scores ">
										<img src="../../icons/flo_s.png"></li>
									<li v-if=" 2 <= item.scores ">
										<img src="../../icons/flo_s.png"></li>
									<li v-if=" 3 <= item.scores ">
										<img src="../../icons/flo_s.png"></li>
									<li v-if=" 4 <= item.scores ">
										<img src="../../icons/flo_s.png"></li>
									<li v-if=" 5 <= item.scores ">
										<img src="../../icons/flo_s.png"></li>
									<li v-if=" 1 > item.scores ">
										<img src="../../icons/flo_k.png"></li>
									<li v-if=" 2 > item.scores ">
										<img src="../../icons/flo_k.png"></li>
									<li v-if=" 3 > item.scores ">
										<img src="../../icons/flo_k.png"></li>
									<li v-if=" 4 > item.scores ">
										<img src="../../icons/flo_k.png"></li>
									<li v-if=" 5 > item.scores ">
										<img src="../../icons/flo_k.png"></li>
								</ul>
								<span style="color:#7b7b7b;">{{item.scores}}.0 分</span>
							</div>
						</div>
					</div>
				</div>
				<!-- 分页 -->
				<div class="pages" v-if="comment.length >= 5">
					<ul>
						<li style="color: #FFDE3A;border-color: #FFDE3A;">1</li>
						<li>2</li>
						<li>></li>
					</ul>
				</div>
	  	</div>
  	</div>
		<div class="footer">
      © 2017 HangZhou. All Rights Reserved | Design by Thinking.
    </div>	
  </div>
</template>
<script>
  export default {
    data () {
      return {
        starNum: 0,
        commentText: '',
        comment: [
          {
            name: '有毒的黄同学',
            headp: 'static/hp1.jpg',
            time: '2016-08-17',
            content: '好,111111讲得非常好，good',
            like: 7,
            like_show: 'like',
            dislike_show: 'dislike',
            dislike: 3,
            scores: 3
          },
          {
            name: 'Freedom小黄',
            headp: 'static/hp3.jpg',
            time: '2016-08-17',
            content: '好,22222讲得非常好，good',
            like: 2,
            like_show: 'like',
            dislike_show: 'dislike',
            dislike: 3,
            scores: 5
          }
        ]
      }
    },
    methods: {
      showStar (num) {
        this.starNum = num
      },
      addComment () {
        this.comment.push({
          name: '我',
          headp: 'static/hp1.jpg',
          time: getTime(),
          content: this.commentText,
          like_show: 'like',
          dislike_show: 'dislike',
          like: 0,
          dislike: 0,
          scores: this.starNum
        })
        this.commentText = ''
        this.starNum = 0
      },
      ChangeLike (item, str, n) {
        item.like_show = str
        item.like += n
      },
      ChangeDislike (item, str, n) {
        item.dislike_show = str
        item.dislike += n
      }
    }
  }
  function getTime () {
    var now = new Date()
    var year = now.getFullYear()
    var month = now.getMonth() + 1
    var day = now.getDate()
    month.length < 2 ? '0' + month : month
    day.length < 2 ? '0' + day : day
    return year + '-' + month + '-' + day
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/remark.css';
.main_re{
	width: 1200px;
	margin: 0 auto;
}
.main_le{
	width: 70%;
	float: left;
}
.hengg1{
		height: 2px;
		width: 100%;
		margin: 0 auto;
		background: #ccc;
	}

</style>



