<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active"><a href="##">我的景点</a></span>
    </div>
    <div class="midr2 content">
      <div>
        <router-link class="btn btn1" :to="{path: '/add/jingdianAdd', query:{type: 'sight'}
        }" target="_blank">
          添加景点
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myJd, index) in myJingdian">
        <div style="width: 300px;float: left;" class="imgcenter">
          <img :src="myJd.jdImg">
        </div>
        <div class="cellr">
          <router-link :to="{path: '/displayremark/sight', query: { id: myJd.jdId, type: 'sight' }}" target="_blank">
            <h4 v-text="myJd.jdName"></h4>
          </router-link>
          <p class="">{{myJd.location}}</p>
          <p class="" v-text="myJd.jdTime"></p>
          <p>
            <router-link class="btn btn-sm btn-gray" :to="{path: '/add/jingdianAdd', query: { id: myJd.jdId, type: 'sight', type2: 'change' }}" target="_blank">
              修改
            </router-link>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        myJingdian: [
          {
            jdName: '大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷',
            jdImg: 'static/special1.jpg',
            jdId: 1,
            jdTime: '2017/3/7',
            location: '浙江省 湖州市 安吉县 递铺镇 余村村'
          },
          {
            jdName: '峡谷大峡谷大峡谷大峡谷大峡谷峡谷大峡谷大峡谷',
            jdImg: 'static/special2.jpg',
            jdId: 2,
            jdTime: '2017/3/7',
            location: '浙江省 湖州市 安吉县 递铺镇 余村村'
          }
        ]
      }
    },
    created () {
      this.getPersonJingdian()
    },
    methods: {
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 获取 引dateFormat */
      getPersonJingdian () {
        const self = this
        axios.get('userJingdian/UserJingdians').then(function (res) {
          self.myJingdian = res.data.Jingdian
          for (var i = 0; i < self.myJingdian.length; i++) {
            self.myJingdian[i].jdTime = self.dateFormat(self.myJingdian[i].jdTime)
          }
          console.log('获取个人景点成功！')
        }).catch(function (err) {
          console.log('获取个人景点失败：' + err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



