<template>
  <div>
  <!-- 封面 -->
		<div class="cover">
			<img :src="sCover">
			<div class="pictures" @click="showChange('modal-img')">查看图片</div>
		</div>

		<div class="main clear">
			<div class="main_left">
			<!-- 回到顶部 -->
				<div class="backToTop transition1 rotateY" @click="scrollUpFun()">
                    <img src="../../icons/toup2.png">
                </div>
			<!-- 定位 -->
				<div class="located">
					<ul>
						<li style="border-bottom: 3px solid #FFDE3A;" @click="scrollFun(500)">综诉</li>
						<li @click="scrollFun(1500)">位置</li>
						<li @click="scrollFun(2050)">评价</li>
					</ul>
				</div>
			<!-- 名称栏 -->
				<div class="namecon">
					<div class="na_sc clear">
						<div class="spo_name">{{sName}}</div>
						<div class="spo_score">
							<ul>
                <li v-if=" 1 <= sScore ">
                  <img src="../../icons/flo_s.png"></li>
                <li v-if=" 2 <= sScore ">
                  <img src="../../icons/flo_s.png"></li>
                <li v-if=" 3 <= sScore ">
                  <img src="../../icons/flo_s.png"></li>
                <li v-if=" 4 <= sScore ">
                  <img src="../../icons/flo_s.png"></li>
                <li v-if=" 5 <= sScore ">
                  <img src="../../icons/flo_s.png"></li>
                <li v-if=" 1 > sScore ">
                  <img src="../../icons/flo_k.png"></li>
                <li v-if=" 2 > sScore ">
                  <img src="../../icons/flo_k.png"></li>
                <li v-if=" 3 > sScore ">
                  <img src="../../icons/flo_k.png"></li>
                <li v-if=" 4 > sScore ">
                  <img src="../../icons/flo_k.png"></li>
                <li v-if=" 5 > sScore ">
                  <img src="../../icons/flo_k.png"></li>
						  </ul>
							<div>{{sScore}}.0分</div>
						</div>
					</div>
					<div class="short_addr clear">浙江省&nbsp;&nbsp;湖州市&nbsp;&nbsp;安吉县&nbsp;&nbsp;{{sBelongTo}}</div>
				</div>
			<!-- 简介 -->
				<div class="introduce clear">
					<ul>
						<li>简介：</li>
						<li>{{sIntroduction}}</li>
					</ul>
				</div>
			<!-- 联系方式 -->
				<div class="contact clear">
					<ul>
						<li>联系方式：</li>
						<li>{{sContact}}</li>
					</ul>
				</div>
			<!-- 视频 -->
        <div class="spo_video clear">
          <video width="100%" height="100%" controls>
            <source :src="sVideo"  type="video/mp4">
          </video>
        </div>
				<div class="hengg1 clear"></div>
			<!-- 详细地址 -->
				<div class="address clear">
					<ul>
						<li>详细地址：</li>
						<li>{{sAddress}}</li>
					</ul>
				</div>
			<!-- 位置显示 -->
				<div class="map"></div>
				<div class="hengg1 clear"></div>
			</div>

		<!-- 右侧 -->
			<div class="main_right">
				<div class="price">{{priceName2}}&nbsp; &nbsp; <span style="font-size:20px;font-weight:bold;">{{sTicket}}RMB</span>&nbsp; &nbsp; {{priceName3}}</div>
				<div class="support">
					<div class="sup_btn" v-if="good_show === 'good'" @click="ChangeGood('gooded', 1)">
						<img src="../../icons/good.png">
						喜欢就要行动
					</div>
					<div class="sup_btn" v-if="good_show === 'gooded'" @click="ChangeGood('good', -1)">
						<img src="../../icons/gooded.png">
						喜欢就要行动
					</div>
					<div class="sup_count">
						<span>{{sLikers}}</span>
						位游客做出了行动
					</div>
					<div class="share">
						<img src="../../icons/wechated.png">
						<img src="../../icons/sinaed.png">
						<img src="../../icons/mailed.png">
						<img src="../../icons/pointes.png">
					</div>
				</div>
				<div class="wrong">
					<img src="../../icons/send.png">
					<router-link :to="{ path: '/show/finderror' }" class="aFont" target="_blank">举报该{{priceName}}</router-link>
				</div>
			</div>
		</div>
		<!--图片模态框-->
    <div class="modal" v-if="showModalValue === 'modal-img'">
      <div class="modal-con modal-con-noColor">
        <div class="modal-con-img">
          <button class="btn-close" @click="showChange('close')"></button>
          <div class="modal-img-btn modal-img-btn-left" @click="showBefore()" @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
          <!--<img src="../../images/view.jpg" alt="">-->
          <img v-for="(image, index) in imgInfo" :src="image" v-if="showImgIndex === index">
          <div class="modal-img-btn modal-img-btn-right" @click="showAfter()"  @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        good_show: 'good',
        scroll: 0,
        startLocation: 0,
        showModalValue: '',
        showButtonValue: false,
        showImgIndex: 0,    /* 当前显示的图片的索引号 */
        // 图片集
        imgInfo: [],
        sScore: 5,
        // 隶属
        sBelongTo: '',
        // 简介
        sIntroduction: '',
        // 电话
        sContact: '',
        // 地址
        sAddress: '',
        // 价格
        sTicket: '未知',
        sLikers: 0,
        // 封面
        sCover: '',
        // 视频
        sVideo: '',
        showbackToTop: false,
        /* 前台数据 */
        dataQianTai: {
          name: '',
          jianjie: '',
          price: '',
          phone: '',
          adress: '',
          fengmian: '',
          images: '',
          video: '',
          location: ''
        },
        // 名字
        sName: '',
        // 唯一不同的列名
        priceName: '',
        priceName2: '',
        priceName3: ''
      }
    },
    created () {
      this.menun()
      if (this.GetQueryString('type') === 'sight') {
        this.priceName = '景点'
        this.priceName2 = '门票'
        this.priceName3 = '每人'
      } else if (this.GetQueryString('type') === 'canguan') {
        this.priceName = '餐馆'
        this.priceName2 = '人均'
        this.priceName3 = '每餐'
      } else if (this.GetQueryString('type') === 'hotle') {
        this.priceName = '住宿'
        this.priceName2 = '低至'
        this.priceName3 = '每晚'
      } else {
        this.priceName = '地'
        this.priceName2 = '人均消费'
        this.priceName3 = ''
      }
      this.getData()
    },
    methods: {
      ChangeGood (str, n) {
        this.good_show = str
        this.sLikers += n
      },
      menun () {
        window.scrollTo(0, 0)
      },
      scrollFun (num) {
        let n = Math.floor(num / 10) * 10
        let timer = setInterval(() => {
          /* window.alert(this.step) */
          if (this.scroll < n) {
            window.scrollTo(this.scroll, this.scroll + 10)
            this.scroll += 10
          } else {
            if (this.scroll < num) {
              window.scrollTo(this.scroll, num)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      scrollUpFun () {
        this.startLocation = this.scroll
        let n = this.startLocation - Math.floor(this.startLocation / 10) * 10
        let timer = setInterval(() => {
          if (this.scroll > n) {
            window.scrollTo(this.scroll, this.scroll - 10)
            this.scroll -= 10
          } else {
            if (this.scroll > 0) {
              window.scrollTo(n, 0)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      menu () {
        this.scroll = document.body.scrollTop
        var btnBacktop = document.getElementsByClassName('backToTop')[0]
        if (this.scroll < 500) {
          btnBacktop.style.opacity = '0'
        } else {
          btnBacktop.style.opacity = '1'
        }
        // console.log(this.scroll)
      },
      showChange (obj) {
        if (obj === 'modal-img') {
          this.showModalValue = 'modal-img'
        } else if (obj === 'modal-text') {
          this.showModalValue = 'modal-text'
        } else {
          this.showModalValue = ''
        }
      },
      showBtn () {
        this.showButtonValue = !this.showButtonValue
      },
      showBefore () {
        if (this.showImgIndex === 0) {
          this.showImgIndex = this.imgInfo.length - 1
        } else {
          this.showImgIndex -= 1
        }
      },
      showAfter () {
        if (this.showImgIndex === this.imgInfo.length - 1) {
          this.showImgIndex = 0
        } else {
          this.showImgIndex += 1
        }
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      /* 格式化img的图片地址信息 */
      replaceFun1 (str) {
        if (str !== null) {
          return str.replace(/%3A/g, ':').replace(/%2F/g, '/')
        } else {
          return ''
        }
      },
      // 假设 res.data.info 假设其变量名
      getData () {
        console.log('得到的id：' + this.GetQueryString('id'))
        axios.get('sight/info', this.GetQueryString('id')).then(function (res) {
          console.log('获取资料成功')
          this.sName = res.data.info.name
          this.imgInfo = res.data.info.images.split(',')
          this.sBelongTo = res.data.info.location
          this.sIntroduction = res.data.info.jianjie
          this.sContact = res.data.info.phone
          this.sAddress = res.data.info.adress
          this.sTicket = res.data.info.price
          this.sCover = res.data.info.fengmian
          this.sVideo = res.data.info.video
        }).catch(function (err) {
          console.log('获取资料失败')
          console.log(err)
        })
        console.log('获取到的数据如下：')
        console.log('名字：')
        console.log(this.sName)
        console.log('简介：')
        console.log(this.sIntroduction)
        console.log('隶属：')
        console.log(this.sBelongTo)
        console.log('图片集：')
        console.log(this.imgInfo)
        console.log(this.GetQueryString('images'))
        console.log('联系方式：')
        console.log(this.sContact)
        console.log('详细地址：')
        console.log(this.sAddress)
        console.log('价格：')
        console.log(this.sTicket)
        console.log('封面：')
        console.log(this.sCover)
        console.log('视频：')
        console.log(this.sVideo)
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/srh.css';
@import '../../styles/model.css';
@import '../../styles/move.css';
</style>



