<template>
  <div id="app">
    <div class="nav">
      <div class="logo">
        <router-link :to="{ path: '/' }" class="aFont">LOGO</router-link>
      </div>
      <div class="loadAndRe" v-show="!showuser">
        <ul>
          <li><router-link :to="{ path: '/loadandreg/load' }" class="aFont">登录</router-link></li>
          <li><router-link :to="{ path: '/loadandreg/register' }" class="aFont">注册</router-link></li>
        </ul>
      </div>
      <div class="headp" v-show="showuser"><!-- @mouseout="changeOneThing(false)" @mouseover="changeOneThing(true)"  -->
        <img src="./images/hp1.jpg" v-show="showhp"@mouseout="changeOneThing(false)" @mouseover="changeOneThing(true)"><!-- 
        <img src="./images/hp1.jpg" v-show="!showhp" @click="changeThings(false,true)"> -->
        <ul v-show="showoption"  @mouseout="changeOneThing(false)" @mouseover="changeOneThing(true)">
          <li @click="changeThings(false,true)"><router-link :to="{ path: '/person/youji' }" class="aFontColor">Silence12344</router-link></li>
          <li @click="changeThings(false,true)">退出登录</li>
        </ul>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      showoption: false,
      showhp: true,
      showuser: false,
      userInfo: {
        userName: '最新的用户名',
        signature: '这位同学很懒，木有签名的说～',
        userPic: '/static/t16.jpg',
        dengji: 1,
        fensi: 2,
        guanzu: 3,
        jingyan: 4,
        sex: '女',
        birthday: '2017-05-20',
        city: '上海'
      }
    }
  },
  methods: {
    changeThings (str1, str2) {
      this.showoption = str1
      this.showhp = str2
    },
    changeOneThing (str1) {
      this.showoption = str1
    }
  }
}
</script>

<style scoped>
@import './styles/main.css';
  .aFontColor{
    color: #fff;
    text-decoration: none;
  }
  .nav{
    height: 60px;
    width: 100%;
  }
  .logo{
    width: 80px;
    height: 40px;
    color: #FFDE3A;
    font-size: 29px;
    font-weight: bold;
    /*background: rgba(110,120,144,0.7);*/
    float: left;
    margin: 10px 10px 10px 40px;
  }
  .loadAndRe{
    float: right;
    margin: 20px 50px;
  }
  .loadAndRe li{
    height: 20px;
    line-height: 20px;
    width: 60px;
    font-size: 18px;
    text-align: center;
    float: left;
    cursor: pointer;
  }
  .loadAndRe li:nth-child(1){
    border-right: 2px solid #000;
  }
  .headp{
    height: 50px;
    width: 50px;
    float: right;
    margin: 5px 80px;
    border-radius: 50%;
    position: relative;
  }
  .headp img{
    width: 100%;
    height: 100%;
    cursor: pointer;
    border-radius: 50%;
  }
  .headp ul{
    position: absolute;
    width: 110px;
    top: 50px;
    left: 0px;
    background: rgba(0,2,0,0.9);
    z-index: 20;
  }
  .headp ul li{
    height: 40px;
    color: #fff;
    line-height: 40px;
    padding-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-top: 1px solid #000;
    cursor: pointer;
  }
  .headp ul li:hover{
    background: #292929;
  }

</style>



