    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        axios.get('user/canguan1').then(function (res) {
          this.myJingdian = res.data
        }).catch(function (err) {
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



