<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span :class="{active: show === 'tab1'}"><a href="javascript:;" @click="showTab('tab1')">修改个人信息</a></span>
      <span :class="{active: show === 'tab2'}"><a href="javascript:;" @click="showTab('tab2')">修改密码</a></span>
      <span :class="{active: show === 'tab3'}"><a href="javascript:;" @click="showTab('tab3')">个人认证</a></span>
    </div>
    <!-- 修改个人资料 -->
    <div class="midr3 content" v-if="show === 'tab1'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>昵称：</span>
          <input type="text" class="text-input" v-model="userInfo2.username">
        </p>
        <div class="formtr formtrDiv clear">
          <span>头像：</span>
          <div class="xc_cover2">
            <img :src="userInfo2.headPic" alt="">
          </div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input"  @change="imageInputFun('file-btn-input','xc_cover2',0)">
          </a>
        </div>
        <p class="formtr formtrp">
          <span>性别：</span>
          <input type="radio" value="0" name="sex" :checked="userInfo2.sex === '0'" @click="userInfo2.sex='0'">
          <lable class="input_radio" for="sex">男</lable>
          <input type="radio" value="1" name="sex" :checked="userInfo2.sex === '1'" @click="userInfo2.sex='1'">
          <lable class="input_radio" for="sex">女</lable>

        </p>
        <!--<p class="formtr formtrp">
          <span>生日：</span>
          <input type="text" class="text-input" placeholder="2017-01-01" v-model="userInfo2.birth">
        </p>-->
        <p class="formtr formtrp">
          <span>手机：</span>
          <input type="text" class="text-input" v-model="userInfo2.phone">
        </p>
        <!--<p class="formtr formtrp">
          <span>微信：</span>
          <input type="text" class="text-input" v-model="userInfo2.wechat">
        </p>-->
        <p class="formtr formtrp">
          <span>居住城市：</span>
          <input type="text" class="text-input" placeholder="上海"  v-model="userInfo2.city">
        </p>
        <!--<p class="formtr formtrp">
          <span>个性签名：</span>
          <input type="text" class="text-input" v-model="userInfo2.signature">
        </p>-->
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendDataFun">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <!-- 修改密码 -->
    <div class="midr2 content" v-if="show === 'tab2'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>旧密码：</span>
          <input type="password" class="text-input" v-model="passwd1">
        </p>
        <p class="formtr formtrp">
          <span>新密码：</span>
          <input type="password" class="text-input" v-model="passwd2">
        </p>
        <p class="formtr formtrp">
          <span>再次输入：</span>
          <input type="password" class="text-input" v-model="passwd3">
        </p>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendPasswd">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <!-- 个人认证 -->
    <div class="midr4 content" v-if="show === 'tab3'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span class="must" style="width: 2px">*</span></span><span>姓名：</span>
          <input type="text" class="text-input" v-model="rz.name">
        </p>
        <p class="formtr formtrp">
          <span class="must" style="width: 2px">*</span><span>证件号：</span>
          <input type="text" class="text-input" v-model="rz.number">
        </p>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">*</span><span>身份证正面：</span>
          <div class="xc_cover rz-pic1" style="margin: 0px 0px 25px 140px;">请选择身份证正面</div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input rz-pic-btn1" id="" multiple="multiple" @change="setImagePreviews1('rz-pic-btn1','rz-pic1',0)">
          </a>
        </div>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">*</span><span>身份证反面：</span>
          <div class="xc_cover rz-pic2" style="margin: 0px 0px 25px 140px;">请选择身份证反面</div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input rz-pic-btn2" id="" multiple="multiple" @change="setImagePreviews1('rz-pic-btn2','rz-pic2',0)">
          </a>
        </div>
        <div class="formtr formtrDiv">
          <span class="must" style="width: 2px">&nbsp;</span><span>备注：</span>
          <textarea class="textarea" rows="13" v-model="xcIntroduction" style="width:63.5%;"></textarea>
        </div>
        <div class="formtr formtrDiv clear">
          <span class="must" style="width: 2px">&nbsp;</span><span>备注图片：</span>
          <div class="xc_cover rz-pic3" style="margin: 0px 0px 25px 140px;">请选择备注图片</div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input" style="margin-left: 20px">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input rz-pic-btn3" id="" multiple="multiple" @change="setImagePreviews1('rz-pic-btn3','rz-pic3',0)">
          </a>
        </div>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendRz">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'tab1',
        userName: this.userInfo.userName,
        sex: this.userInfo.sex,
        birthday: this.userInfo.birthday,
        city: this.userInfo.city,
        signature: this.userInfo.signature,
        phone: this.userInfo.phone,
        wechat: this.userInfo.wechat,
        tx: this.userInfo.userPic,
        passwd1: '',
        passwd2: '',
        passwd3: '',
        /* 页面显示的个人资料 */
        userInfo: {
          userName: '阿水12344',
          signature: '这位同学很懒，木有签名的说～',
          userPic: '../../../static/t16.jpg',
          dengji: 1,
          fensi: 2,
          guanzu: 3,
          jingyan: 4,
          sex: 1,
          birthday: '2017-05-20',
          city: '上海',
          phone: 'aaa',
          wechat: 'aaa',
          idcard: 'ss'
        },
        /* 临时存放 个人资料 */
        userInfo2: {
          username: '无名',
          sex: '0',
          birth: '2017-01-01',
          city: '杭州',
          signature: '无',
          headPic: '../../../static/mrtx.jpg',
          phone: '1782680****',
          wechat: 'wm1782680****',
          level: '0',
          fans: '0',
          attention: '0',
          experience: '0'
        },
        /* 后台 密码 */
        password: {
          oldpwd: '',
          newpwd: ''
        },
        /* 后台 个人资料 表单格式 param */
        param: new window.FormData(),
        rz: {
          name: '111',
          number: 'qqqq',
          pic: 'aaa'
        }
      }
    },
    methods: {
      showTab (str) {
        this.show = str
      },
      /* 修改密码 */
      sendPasswd () {
        if (this.passwd2 !== this.passwd3) {
          window.alert('两次输入的密码不相同！')
        } else if (!/^([a-zA-Z0-9]|[_]){6,20}$/.test(this.passwd2)) {
          window.alert('密码长度6-20位，且只能包含符号‘_’')
        } else {
          this.password.oldpwd = this.passwd1
          this.password.newpwd = this.passwd2
          axios.post('user/changepwd', this.password, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            }
          }).then(function (res) {
            if (res.data === '修改密码成功') {
              window.alert('修改密码成功')
            } else if (res.data === '旧密码不正确') {
              window.alert('旧密码不正确')
            } else {
              window.alert('修改密码失败')
            }
          }).catch(function (err) {
            console.log(err)
          })
        }
      },
      /* 获取个人资料 */
      getPersonInfo () {
        const self = this
        axios.get('user/info').then(function (res) {
          console.log('第二成功获取info1数据：' + res)
          self.userInfo.userName = res.data.user[0].username
          self.userInfo.userPic = 'img/headpic/' + res.data.user[0].headpic
          if (res.data.user[0].sex === '男') {
            self.userInfo.sex = '0'
          } else {
            self.userInfo.sex = '1'
          }
          self.userInfo.birthday = self.dateFormat(res.data.user[0].birth)
          self.userInfo.city = res.data.user[0].city
          self.userInfo.signature = res.data.user[0].signature
          self.userInfo.phone = res.data.user[0].phone
          self.userInfo.wechat = res.data.user[0].wechat
          self.userInfo.dengji = res.data.user[0].level
          self.userInfo.jingyan = res.data.user[0].experience
          self.userInfo.fensi = res.data.user[0].fans
          self.userInfo.guanzu = res.data.user[0].attention
          self.userInfo2.username = self.userInfo.userName
          self.userInfo2.sex = self.userInfo.sex
          self.userInfo2.city = self.userInfo.city
          self.userInfo2.headPic = self.userInfo.userPic
          self.userInfo2.birth = self.userInfo.birthday
          self.userInfo2.signature = self.userInfo.signature
          self.userInfo2.phone = self.userInfo.phone
          self.userInfo2.wechat = self.userInfo.wechat
        }).catch(function (err) {
          console.log('第二获取个人资料失败：' + err)
        })
      },
      /* 修改个人资料 */
      sendDataFun () {
        this.param.set('username', this.userInfo2.username)
        this.param.set('sex', this.userInfo2.sex)
        // this.param.set('birth', this.userInfo2.birth)
        this.param.set('city', this.userInfo2.city)
        // this.param.set('signature', this.userInfo2.signature)
        this.param.set('phone', this.userInfo2.phone)
        // this.param.set('wechat', this.userInfo2.wechat)
        console.log('要上传的数据：{用户名：' + this.param.get('username') + ',性别： ' + this.param.get('sex') + ',城市： ' + this.param.get('city') + ',头像： ' + this.param.get('headpic') + ',手机： ' + this.param.get('phone'))
        console.log('头像：')
        console.log(this.param.get('headpic'))
        axios.post('user/changeUserInfo', this.param, {
          headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data.info === '修改成功') {
            window.alert('修改成功~')
            this.getPersonInfo()
            /* window.location.reload() */
          } else if (res.data.info === '用户名已存在') {
            window.alert('用户名已存在~')
            window.location.reload()
          } else if (res.data.info === '手机号已注册') {
            window.alert('手机号已注册~')
            window.location.reload()
          } else {
            window.alert('修改失败~')
            window.location.reload()
          }
        }).catch(function (err) {
          console.log(err)
        })
      },
      /* 时间格式化 */
      dateFormat (time) {
        var date = new Date(time)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        return year + '-' + month + '-' + day
      },
      /* 上传单张图片 */
      imageInputFun (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.userInfo2.userPic = imgObjPreview.src
          this.param.set('headpic', choosePic.files[0], choosePic.files[0].name)
        }
      },
      /* 认证 */
      sendRz () {},
      /* 封面图片添加函数 */
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.yjAdd.fengmian = imgObjPreview.src
          this.sendData.set('mainPic', choosePic.files[0], choosePic.files[0].name)
        }
      }
    },
    props: ['userInfo'],
    created () {
      this.getData()
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
</style>



