  mounted () {
		/* 地图 */
    var map = new AMap.Map("container", {
      resizeEnable: true
    });
    const self = this;
    AMap.service(["AMap.PlaceSearch"], function() {
      var placeSearch = new AMap.PlaceSearch({ //构造地点查询类
        pageSize: 1,
        pageIndex: 1,
        city: "全国", //城市
        map: map,
      });
      //关键字查询
      self.placeSearch = placeSearch;
    });
  },
  methods: {
		/* 获取乡村信息 */
		getContryside () {
			const self = this
			axios.get('country/allInfo', this.GetQueryString('id'), {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
				}
			}).then(function (res) {
				console.log(res.data.info)
				console.log('\n\n\n\n\n获取乡村信息如下：')
				this.cCover = res.data.country.mianpic
				this.cName = res.data.country.name
				this.cIntroduction = res.data.country.introduce
				this.location = res.data.country.location
				this.countrytype = res.data.country.countrytype
				this.imgsInfo = res.data.
				this.videoInfo = res.data.
				this.cCulture = res.data.country.culture
				this.actInfo = res.data.country.activities
				this.techanInfo = {}
				var jingdianData = res.data.scenicSpots
				for (var jingdianI = 0; jingdianI < jingdianData.length; jingdianI++) {
					this.jingdian[jingdianI].src = '#/displayremark/restaurant?id=' + jingdianData[jingdianI].id + '&type=sight'
					this.jingdian[jingdianI].name = jingdianData[jingdianI].name
				}
				var canguanData = res.data.restaurants
				for (var canguanI = 0; canguanI < canguanData.length; canguanI++) {
					this.canguan[canguanI].src = '#/displayremark/restaurant?id=' + canguanData[canguanI].id + '&type=sight'
					this.canguan[canguanI].name = canguanData[canguanI].name
				}
				var zhusuData = res.data.hotels
				for (var zhusuI = 0; zhusuI < zhusuData.length; zhusuI++) {
					this.zhusu[zhusuI].src = '#/displayremark/sigh?id=' + zhusuData[zhusuI].id + '&type=sight'
					this.zhusu[zhusuI].name = zhusuData[zhusuI].name
				}
				this.travelNotes = res.data.articles
				var commentData = res.data.comments
				for (var commentI = 0; commentI < commentData.length; commentI++) {
					this.comment[commentI].name = commentData[commentI].username
					this.comment[commentI].headp = commentData[commentI].headpic
					this.comment[commentI].time = this.dateFormat(commentData[commentI].delivertime)
					this.comment[commentI].content = commentData[commentI].content
					this.comment[commentI].scores = commentData[commentI].score
					this.comment[commentI].like = commentData[commentI].nicesum
					this.comment[commentI].dislike = commentData[commentI].badsum
					this.comment[commentI].like_show = 'like'
					this.comment[commentI].dislike_show = 'dislike'
				}
			}).catch(function (err) {
				console.log('获取乡村信息失败' + err)
			})
		},
		searchPleace(pleace) {
			this.placeSearch.search(pleace);
