      item.like += n
    },
    ChangeDislike (item, str, n) {
      item.dislike_show = str
      item.dislike += n
    },
    changeImgIndex (index) {
      /* window.alert(index) */
      this.nowImgNum = index
    },
    /* 图片的高度与跳动距离有关 */
    btnDown () {
      /* window.alert(1) */
      /* this.style = 'move' */
      /* 用 :class="style" 改变style的值 */
      /* window.alert(this.aaa.length) */
      if (this.techanInfo.length <= 3) {
        return
      } else {
        if (this.nowLocation === 121 * (this.techanInfo.length - 3)) {
          this.nowLocation = 0
        } else {
          this.nowLocation += 121
        }
        this.style2 = this.move
      }
    },
    btnUp () {
      if (this.nowLocation === 0) {
        this.nowLocation = 121 * (this.techanInfo.length - 3)
      } else {
        this.nowLocation -= 121
      }
      this.style2 = this.move
    },
    showChange (obj, index) {
      if (obj === 'modal-img') {
        this.showModalValue = 'modal-img'
        this.imgInfo = this.imgsInfo[index]
      } else if (obj === 'modal-text') {
        this.showModalValue = 'modal-text'
        this.showActIndex = index
      } else if (obj === 'modal-video') {
        this.showModalValue = 'modal-video'
        this.showVidIndex = index
      } else {
        this.showModalValue = ''
      }
    },
    showBtn () {
      this.showButtonValue = !this.showButtonValue
    },
    showBefore () {
      if (this.showImgIndex === 0) {
        this.showImgIndex = this.imgInfo.length - 1
      } else {
        this.showImgIndex -= 1
      }
    },
    showAfter () {
      if (this.showImgIndex === this.imgInfo.length - 1) {
        this.showImgIndex = 0
      } else {
        this.showImgIndex += 1
      }
    },
    showTShow (str) {
      this.shouTShow = str
    },
		/* 获取url中的参数 */
		GetQueryString (name) {
			var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
			var reg2 = new RegExp('([^=]+)$')
			var r = window.location.href.match(reg)
			if (r != null) {
				return r[0].match(reg2)[0]
			} else {
				return null
			}
		},
		dateFormat (time) {
			var date = new Date(time);
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var day = date.getDate();
			var hour = date.getHours();
			var minute = date.getMinutes();
			var second = date.getSeconds();
			console.log(time + '\n' + year + ' ' + month + ' ' + day + ' ' + hour + ' ' + minute + ' ' + second)
			month = (String(month).length < 2) ? '0' + month : month;
			day = (String(day).length < 2) ? '0' + day : day;
			hour = (String(hour).length < 2) ? '0' + hour : hour;
			minute = (String(minute).length < 2) ? '0' + minute : minute;
			second = (String(second).length < 2) ? '0' + second : second;
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
		}

	}
}
function getTime () {
  var now = new Date()
  var year = now.getFullYear()
  var month = now.getMonth() + 1
  var day = now.getDate()
	month = parseInt(month) < 10 ? '0' + month : month
	day = parseInt(day) < 10 ? '0' + day : day
  return year + '-' + month + '-' + day
}
</script>
