          {bgImg: '/static/c5.jpg', name: '马家弄村', introduction: '余村海产品特别丰富，盛产鱼、虾、花蟹。环境优雅，有大片肥沃的农田，大片快速丰产林，是一个鱼米之乡。4'},
          {bgImg: '/static/c6.jpg', name: '高家堂村', introduction: '余村海产品特别丰富，盛产鱼、虾、花蟹。环境优雅，有大片肥沃的农田，大片快速丰产林，是一个鱼米之乡。, num: 5'},
          {bgImg: '/static/c7.jpg', name: '尚书干村', introduction: '余村海产品特别丰富，盛产鱼、虾、花蟹。环境优雅，有大片肥沃的农田，大片快速丰产林，是一个鱼米之乡。, num: 6'},
          {bgImg: '/static/c0.jpg', name: '长林垓村', introduction: '余村海产品特别丰富，盛产鱼、虾、花蟹。环境优雅，有大片肥沃的农田，大片快速丰产林，是一个鱼米之乡。, num: 7'}
        ],
        showCountry: '横山坞村',
        showText: '余村海产品特别丰富，盛产鱼、虾、花蟹。环境优雅，有大片肥沃的农田，大片快速丰产林，是一个鱼米之乡。',
