          {src: '/static/area13.png', num: 13},
          {src: '/static/area14.png', num: 14},
          {src: '/static/area15.png', num: 15}
        ],
        // 模态框中有的
        showModalValue: '',
        showButtonValue: false,
        showImgIndex: 0,
        imgInfo: ['/static/special1.jpg', '/static/special2.jpg', '/static/special3.jpg'],
        videoInfo: { src: '/static/mp4_video.mp4', title: '壮丽河山', cover: '/static/fallback.jpg' },
        destination: [
          {country: '高家堂村', spot: [ '藏龙大瀑布11', '中南百草园' ], restaurant: [ '餐馆1', '餐馆2' ], hotel: [ '住宿1', '住宿2' ]},
          {country: '余村村', spot: [ '藏龙大瀑布11', '中南百草园' ], restaurant: [ '餐馆1', '餐馆2' ], hotel: [ '住宿1', '住宿2' ]},
          {country: '横山坞村', spot: [ '藏龙大瀑布11', '中南百草园' ], restaurant: [ '餐馆1', '餐馆2' ], hotel: [ '住宿1', '住宿2' ]}
        ],
        iconMove1: false,
        iconMove2: false,
        iconMove3: false
      }
    },
    methods: {
      changeArea (num) {
        this.showArea = num
      },
      text () {
        window.alert(1)
      },
      zuobiao (event) {   /* 点定位函数，调用方法：@click="zuobiao($event) ，距离浏览器多高，被滚动条卷去的部分不算" */
        window.alert('Y 坐标: ' + event.clientY + ', X 坐标: ' + event.clientX)
      },
      changeText (num) {
        this.showCountry = this.countryInfor[num].name
        this.showText = this.countryInfor[num].introduction
        this.nowCountryImg = this.countryInfor[num].bgImg
      },
      // 模态框中包含的
      showChange (obj) {
        if (obj === 'modal-img') {
          this.showModalValue = 'modal-img'
        } else if (obj === 'modal-text') {
          this.showModalValue = 'modal-text'
          // this.showActIndex = index
        } else if (obj === 'modal-video') {
          this.showModalValue = 'modal-video'
          // this.showVidIndex = index
        } else {
          this.showModalValue = ''
        }
      },
      showBtn () {
        this.showButtonValue = !this.showButtonValue
      },
      showBefore () {
        if (this.showImgIndex === 0) {
          this.showImgIndex = this.imgInfo.length - 1
        } else {
          this.showImgIndex -= 1
        }
      },
      showAfter () {
        if (this.showImgIndex === this.imgInfo.length - 1) {
          this.showImgIndex = 0
        } else {
          this.showImgIndex += 1
        }
      },
      // 添加动画 关于我们
      addAnimate (num) {
        if (num === 0) {
          this.iconMove1 = true
        } else if (num === 1) {
          this.iconMove2 = true
        } else {
          this.iconMove3 = true
        }
      },
      // 移除动画 关于我们
      removeAnimate (num) {
        if (num === 0) {
          this.iconMove1 = false
        } else if (num === 1) {
          this.iconMove2 = false
        } else {
          this.iconMove3 = false
        }
      }
    }
  }
</script>

