<template>
  <div class="centerinput clear">
    <img src="../../images/loadbg1.jpg">
    <div class="centerbox clear">
      <h2>欢迎来到&nbsp;&nbsp;&nbsp;玩美乡村</h2>
      <ul>
      	<li><router-link :to="{ path: '/loadandreg/load' }" class="aFontColor">登录</router-link></li>
      	<li><router-link :to="{ path: '/loadandreg/register' }" class="aFontColor">注册</router-link></li>
      </ul>
    	<router-view></router-view>
    </div>
  </div>
</template>
<script>
  export default {
  }
</script>
<style scoped>
@import '../../styles/main.css';
.aFontColor{
    color: #fff;
    text-decoration: none;
  }
.centerinput{
	width: 900px;
	height: 550px;
	position: relative;
	margin: 20px auto;
	background: rgba(111,222,55,0.8);
	box-shadow: 5px 8px 10px 4px #cccccc;
}
.centerinput img{
	width: 100%;
	height: 100%;
}
.centerbox{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
	background: rgba(0,1,0,0.5);
}
h2{
	color: #FFDE3A;
	margin: 20px 30px;
}
.centerbox ul{
	width: 94%;
	height: 30px;
	margin: 0px 3%;
	/*background: rgba(111,222,55,0.8);*/
	border-bottom: 2px solid rgba(225,225,225,0.2);
}
.centerbox ul li{
	color: #fff;
	font-size: 18px;
	height: 30px;
	float: left;
	/*line-height: 30px;*/
	margin-left: 20px;
}
</style>



