<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        jdCover: '',
        jdPictures: [],
        /* 前台数据 */
        dataQianTai: {
          name: '',
          jianjie: '',
          price: '',
          phone: '',
          adress: '',
          fengmian: '',
          images: '',
          video: '',
          location: ''
        },
        lishuxian: '递铺镇',
        lishuxiang: '余村村',
        priceName: '',
        priceName2: '',
        // 后台 数据
        paramDataInfo: new window.FormData()
      }
    },
    created () {
      this.menun()
      if (this.GetQueryString('type') === 'sight') {
        this.priceName = '门票'
        this.priceName2 = '景点'
      } else if (this.GetQueryString('type') === 'canguan') {
        this.priceName = '人均消费'
        this.priceName2 = '餐馆'
      } else if (this.GetQueryString('type') === 'hotle') {
        this.priceName = '最低价格'
        this.priceName2 = '住宿'
      } else {
        this.priceName = '人均消费'
        this.priceName2 = ''
      }
      if (this.GetQueryString('type2')) {
        this.getOldData(this.GetQueryString('id'))
      }
    },
    methods: {
      menun () {
        window.scrollTo(0, 0)
      },
      // 封面图片添加函数
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.xcCover = imgObjPreview.src
          this.dataQianTai.fengmian = imgObjPreview.src
          this.paramDataInfo.set('fengmian', choosePic.files[0], choosePic.files[0].name)
        }
      },
      // 照片集添加需改正
      setImagePreviews2 () {
        var choosePic = document.getElementsByClassName('files-input')[0]
        var showPic = document.getElementsByClassName('files-div')[0]
        var cLength = choosePic.files.length
        var iLength = this.jdPictures.length
        for (var j = 0; j < cLength; j++) {
          if (/^((?!image).)+/g.test(choosePic.files[j].type)) {
            window.alert('请选择正确的图片格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div class='img-cell" + (j + iLength) + "' style='width: 190px;height: 240px;float:left;margin: 0px 5px 5px 0px'>" + "<img class='pic_coll" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' style='width:190px;height:190px;display: block'><button class='btn btn-sm btn-gray btn-delfun' style='height: 30px;' type='button'>删除</button>"
            this.jdPictures[iLength + j] = window.URL.createObjectURL(choosePic.files[j])
            this.paramDataInfo.set('images' + (iLength + j), choosePic.files[j], choosePic.files[j].name)
            console.log((iLength + j) + ' : ' + this.paramDataInfo.get('images' + (iLength + j)).name)
          }
        }
        this.delPicBtn()
      },
      // 视频添加
      videoFun () {
        var choosePic = document.getElementsByClassName('video-input')[0]
        var showPic = document.getElementsByClassName('video-div')[0]
        if (/^((?!video).)+/g.test(choosePic.files[0].type)) {
          window.alert('请选择正确的视频格式！')
        } else {
          showPic.innerHTML += "<div style='width: 300px;height: 250px;float: left;margin-right: 5px;margin-bottom: 5px;'><video style='display:block;width:100%;height:100%;' controls><source class='video' src='" + window.URL.createObjectURL(choosePic.files[0]) + "' type='video/mp4'></video><button class='btn btn-sm btn-gray btn-delfun-video' type='button' style='height: 30px;margin-top: 10px;'>删除</button></div>"
          this.dataQianTai.video = window.URL.createObjectURL(choosePic.files[0])
          this.paramDataInfo.set('video', choosePic.files[0], choosePic.files[0].name)
          this.delVideoBtn()
        }
      },
      // 删除图片集
      delPicBtn () {
        setTimeout(function () {
          var btns = document.getElementsByClassName('btn-delfun')
          var showPic = document.getElementsByClassName('files-div')[0]
          for (var i = 0; i < btns.length; i++) {
            btns[i].onclick = function () {
              showPic.innerHTML = ''
              this.jdPictures = []
              this.dataQianTai.images = this.jdPictures
            }
          }
        }, 30)
      },
      // 删除视频集
      delVideoBtn () {
        setTimeout(function () {
          var btns = document.getElementsByClassName('btn-delfun-video')
          var showVideo = document.getElementsByClassName('video-div')[0]
          for (var j = 0; j < btns.length; j++) {
            btns[j].onclick = function () {
              showVideo.innerHTML = ''
              this.dataQianTai.video = ''
            }
          }
        }, 30)
      },
      // 后台 发送
      sendDataMessage () {
        this.paramDataInfo.set('type', this.GetQueryString('type'))
        this.paramDataInfo.set('name', this.dataQianTai.name)
        this.paramDataInfo.set('jianjie', this.dataQianTai.jianjie)
        this.paramDataInfo.set('location', this.dataQianTai.location)
        this.paramDataInfo.set('price', this.dataQianTai.price)
        this.paramDataInfo.set('phone', this.dataQianTai.phone)
        this.paramDataInfo.set('adress', this.dataQianTai.adress)
        axios.post('jigdian', this.paramDataInfo, {
          headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data.info === '发送成功') {
            window.alert('提交成功！')
          }
          console.log(res)
        }).catch(function (err) {
          console.log('提交失败！')
          console.log(err)
        })
        console.log('要发送的数据如下：')
        console.log('---类型---')
        console.log(this.paramDataInfo.get('type'))
        console.log('---标题---')
        console.log(this.paramDataInfo.get('name'))
        console.log('---简介---')
        console.log(this.paramDataInfo.get('jianjie'))
        console.log('---隶属---')
        console.log(this.paramDataInfo.get('location'))
        console.log('---价格---')
        console.log(this.paramDataInfo.get('price'))
        console.log('---联系方式---')
        console.log(this.paramDataInfo.get('phone'))
        console.log('---详细地址---')
        console.log(this.paramDataInfo.get('adress'))
        console.log('---封面---')
        console.log(this.paramDataInfo.get('fengmian'))
        console.log('---图片---')
        for (var imgI = 0; imgI < this.jdPictures.length; imgI++) {
          console.log(imgI + ' : ' + this.paramDataInfo.get('images' + imgI))
        }
        console.log('---视频---')
        console.log(this.paramDataInfo.get('video'))
      },
      // 预览
      yulanFun () {
        // 图片集字符串修改
        for (var imgI = 0; imgI < this.jdPictures.length; imgI++) {
          this.dataQianTai.images += this.jdPictures[imgI]
          if ((imgI + 1) < this.jdPictures.length) {
            this.dataQianTai.images += ','
          }
        }
        // 隶属
        this.dataQianTai.location = '浙江省 湖州市 安吉县' + this.lishuxian + this.lishuxiang
        console.log('预览发送的消息如下：')
        console.log('---标题---')
        console.log(this.dataQianTai.name)
        console.log('---简介---')
        console.log(this.dataQianTai.jianjie)
        console.log('---隶属---')
        console.log(this.dataQianTai.location)
        console.log('---价格---')
        console.log(this.dataQianTai.price)
        console.log('---联系方式---')
        console.log(this.dataQianTai.phone)
        console.log('---详细地址---')
        console.log(this.dataQianTai.adress)
        console.log('---封面---')
        console.log(this.dataQianTai.fengmian)
        console.log('---图片---')
        console.log(this.dataQianTai.images)
        console.log('---视频---')
        console.log(this.dataQianTai.video)
        var a = document.getElementById('ylBtn')
        a.href = '#/displayremark/sight2?name=' + this.dataQianTai.name + '&jianjie=' + this.dataQianTai.jianjie + '&location=' + this.dataQianTai.location + '&price=' + this.dataQianTai.price + '&phone=' + this.dataQianTai.phone + '&adress=' + this.dataQianTai.adress + '&fengmian=' + this.dataQianTai.fengmian + '&images=' + this.dataQianTai.images + '&video=' + this.dataQianTai.video + '&type=' + this.GetQueryString('type')
      },
      /* 获取url中的参数 */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      // 获取数据
      getOldData (id) {
        axios.get('')
      }
    }
  }
</script>

