<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active"><a href="##">我的乡村</a></span>
    </div>
    <div class="midr2 content">
      <div>
        <router-link class="btn btn1" :to="{path: '/add/xiangcunAdd'}" target="_blank">
          添加乡村
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myXc, index) in myCountry">
        <div style="width: 300px;float: left;" class="imgcenter">
          <img :src="myXc.xcImg">
        </div>
        <div class="cellr">
          <router-link :to="{path: '/show/countryside', query: { id: myXc.xcId }}" target="_blank">
            <h4 v-text="myXc.xcName"></h4>
          </router-link>
          <p class="" v-text="myXc.xcTime"></p>
          <p>
            <router-link class="btn btn-sm btn-gray" :to="{path: '/change/xiangcunChange', query: { id: myXc.xcId }}" target="_blank">
              修改
            </router-link>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        myCountry: [
          {
            xcId: '1',
            xcName: '高家堂村村村村村',
            xcTime: '2017/3/7',
            xcImg: 'static/special1.jpg'
          }
        ]
      }
    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        axios.get('country/UserCountrysides').then(function (res) {
          console.log('成功获取商业用户的信息')
          this.myCountry.xcId = res.data.countrysides.id
          this.myCountry.xcName = res.data.countrysides.name
          this.myCountry.xcTime = res.data.countrysides.time
          this.myCountry.xcImg = res.data.countrysides.mianpic
        }).catch(function (err) {
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



