<template>
  <div class="personbar">
    <div class="topbar clear">
      <div class="top top1">
        <!-- <p>LOGO</p> -->
      </div>
      <div class="top top2">
        <img :src="userInfo2.userPic">
      </div>
      <div class="top top3">
        <div class="top3l">
          <br>
          <span v-text="userInfo2.userName" class="username"></span>
          <br>
          <!--<p>
            个性签名：
            <span v-text="userInfo2.signature"></span>
          </p>-->
        </div>
        <div class="top3r">
          <!--<p>
            等级：<span v-text="userInfo2.dengji"></span>
            经验：<span v-text="userInfo2.jingyan"></span>
            关注：<span v-text="userInfo2.guanzu"></span>
            粉丝：<span v-text="userInfo2.fensi"></span>
          </p>-->
        </div>
      </div>
    </div>
    <div class="midbar clear">
      <div class="midl">
        <div class="midl1 mid1"></div>
        <div class="midl2">
          <ul>
            <!--<li>
              <router-link :to="{ path: '/person/youji' }" :class="{active: show === 'youji'}" @click.native="showRouter('youji')">游记管理　></router-link>
            </li>-->
            <li>
              <router-link :to="{ path: '/person/xiangcun' }" :class="{active: show === 'xiangcun'}" @click.native="showRouter('xiangcun')">乡村管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/jingdian' }" :class="{active: show === 'jingdian'}" @click.native="showRouter('jingdian')">景点管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/canguan' }" :class="{active: show === 'canguan'}" @click.native="showRouter('canguan')">餐馆管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/zhusu' }" :class="{active: show === 'zhusu'}" @click.native="showRouter('zhusu')">住宿管理　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/gerenziliao' }" :class="{active: show === 'gerenziliao'}" @click.native="showRouter('gerenziliao')">个人资料　></router-link>
            </li>
            <li>
              <router-link :to="{ path: '/person/tongzhi' }" :class="{active: show === 'tongzhi'}" @click.native="showRouter('tongzhi')">通　　知　></router-link>
            </li>
          </ul>
        </div>
        <div style="clear:both"></div>
      </div>
      <!-- 用户的基本信息，可传给子组件 :user-info="userInfo2 -->
      <router-view :user-info="userInfo2"></router-view>
    </div>
    <div class="footer clear">
      © 2017 HangZhou. All Rights Reserved | Design by Thinking.
    </div>	
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'xiangcun',
        /* 用户的基本信息，可传给子组件 */
        userInfo2: {
          userName: '阿水12344来自userInfo',
          signature: '这位同学很懒，木有签名的说～',
          userPic: 'static/t16.jpg',
          dengji: 1,
          fensi: 2,
          guanzu: 3,
          jingyan: 4,
          sex: 1,
          birthday: '2017-05-20',
          city: '上海',
          phone: '17826808793',
          wechat: 'Lj1060030338',
          idcard: 'ss'
        }
      }
    },
    created () {
      this.getData()
    },
    methods: {
      showRouter (str) {
        this.show = str
        console.log(str)
      },
      getData () {
        axios.get('CountrysideUser/info').then(function (res) {
          console.log('成功获取info1数据：' + res)
          this.userInfo2.city = res.data.countrysideUser.city
          this.userInfo2.phone = res.data.countrysideUser.userphone
          this.userInfo2.sex = res.data.countrysideUser.sex
          this.userInfo2.userName = res.data.countrysideUser.name
          this.userInfo2.userPic = res.data.countrysideUser.headpic
          // this.userInfo2.birthday = res.data.countrysideUser.birth
          // this.userInfo2.signature = res.data.countrysideUser.signature
          // this.userInfo2.wechat = res.data.countrysideUser.wechat
        }).catch(function (err) {
          console.log('未能获取info1数据：' + err)
        })
      }
    }
  }
</script>
<style scoped lang="stylus">
  @import "../../styles/center.css";
  @import "../../styles/style.css";

</style>



